.DBox-c{
    justify-content: space-around;
    /* border: 1px solid; */
    max-width: 373px;
    width: 373px;
    padding: 20px 5px 20px 5px;
    

}
.DBox-c .p {
    font-size: 0.9rem;
    

}
.DBox-c p:nth-child(2) {
    font-size: 1rem;
    font-weight: bold;
    color: rgb(67, 67, 215);
}
.DBox{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 30px 0;
    animation: bottomToTop 1.5s ease-in-out;
 }
 .D-box-B{
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    background-color: var(--dashboard-bg-color);
    background: var(--card-color);
    /* background: linear-gradient(
      -20deg,
      rgb(233, 222, 250) 0%,
      rgb(251, 252, 219) 100%
    ); */
    color: var(--grey-color);
    padding: 10px;
    border-radius: 10px;
 }
 .DBoxGraph{
    line-height: 50px;
    
    
 }
 .DBoxGraph p:nth-child(1){
    /* margin: 0 10px; */
    flex-basis: 22%;
    
 }

 .radialBar-chart-D{
  top: 7px;
  transform: scale(0.6);
  /* animation: zoom-in-out 5s ease infinite;
  transition: animation 1s ease; */
 }

 @keyframes zoom-in-out {
   0% {
     transform: scale(0.9) rotate(30deg);

   }
   50% {
     transform: scale(0.8); 
   }
   100% {
     transform: scale(0.9);
   }
   50% {
      transform: scale(0.8); 
    }
    0% {
      transform: scale(0.9) rotate(30deg);
 
    }
 }

 .table-d-card .table-body{
   height: fit-content;
   max-height: 322px;
   overflow: hidden;
   margin: 0;
   border: 1px solid var(--grey-color);
 }
 .Chart-label-PieDonut{
  overflow: auto;
  overflow-x: hidden;
  height: 350px;
  line-height: 10px;
  font-size: 0.7rem;
  cursor: pointer;
  padding-top: 68px;
  width: 200px;
  scroll-behavior: smooth;
 }

 /* dark mode */
 .dark-mode .apexcharts-pie-series path {
  fill: #ffffff; /* Example: White fill color for pie chart slices in dark mode */
}

.show-more-btn{
  margin: 10px;
  /* float: right; */
  text-align: end;
}