/* AreaChart.css */

@keyframes colorTransition {
    0% {
      background-color: #008FFB;
    }
    33% {
      background-color: #FF4560;
    }
    66% {
      background-color: #33CC33;
    }
    100% {
      background-color: #008FFB;
    }
  }
  
  .color-transition {
    animation: colorTransition 6s infinite; 
  }
  
  
  .label-number{
    font-size: 2rem;
  }