.driverjs-theme {
  background-color: linear-gradient(
    -20deg,
    rgb(233, 222, 250) 0%,
    rgb(251, 252, 219) 100%
  );
  color: #000;
}

.driver-popover.driverjs-theme .driver-popover-title {
  font-size: 20px;
}

.driver-popover.driverjs-theme .driver-popover-title,
.driver-popover.driverjs-theme .driver-popover-description,
.driver-popover.driverjs-theme .driver-popover-progress-text {
  color: var(--grey-color);
}
.tourButton {
  /* border: 1px solid black; */
  margin: 5px;
  padding: 5px 10px;
  border-radius: 15px;
  font-weight: 500;
  cursor: pointer;
  color: #ffff;
  background-color: rgb(2, 100, 236);
}

.driver-popover {
  /* background: var(--card-color); */
  background: rgba(73, 73, 74, 0.348);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(6.5px);
  -webkit-backdrop-filter: blur(6.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  /* transition: all ease-in 1s; */
  /* animation: rotate  2s ease infinite; */
  background: var(--white-color) ;

  color: #ffffff;

  box-sizing: border-box;
  color: #2d2d2d;
  margin: 0;
  padding: 15px;
  border-radius: 5px;
  min-width: 250px;
  max-width: 600px;
  box-shadow: 0 1px 10px #0006;
  z-index: 1000000000;
  position: fixed;
  top: 0;
  right: 0;
  transition: all 0.5s ease-in;
}
@keyframes rotate {
  0% {
    /* transform: rotate(180deg) ; */
    /* background-color: #9b9b9b; */
    /* transform: scale(0.3); */
  }

  100% {
    /* transform: rotate(360deg) ; */
    /* background-color: #c298fa; */
    /* transform: translateY(50px); */
  }
}

.driver-popover-description {
  color: #ffffff;
}
.driver-active-element{
    transition: all ease-in;
}

.driver-popover.driverjs-theme button {
  flex: 1;
  text-align: center;
  background: rgb(197, 121, 255);
  background-color: transparent;
  /* border: 2px solid rgb(231, 169, 255); */
  color: white;
  text-shadow: none;
  font-size: 15px;
  padding: 5px 8px;
  margin-left: 10px;
  border-radius: 5px;
}

.driver-popover.driverjs-theme button:hover {
  background-color: rgb(17, 100, 0) !important;
  background: radial-gradient(
    circle at 10% 20%,
    rgb(0, 52, 89) 0%,
    rgb(0, 168, 232) 90%
  );
  color: #ffffff;
  transition: all 0.5s ease;
  transform: scale(1.1);
}

.driver-popover-close-btn {
  all: unset;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  color: #d2d2d2;
  z-index: 1;
  text-align: center;
  transition: color;
  transition-duration: 2s;
}

.driver-fade .driver-overlay {
    animation: animate-fade-in 2s ease-in-out;
}

.driver-popover-title {
  font: 19px / normal sans-serif;
  font-weight: 700;
  display: block;
  position: relative;
  line-height: 1.5;
  zoom: 1;
  margin: 0;
}

.driver-popover.driverjs-theme .driver-popover-title {
  color: var(--tour-heading);
  font-size: 2rem;
}

.driver-popover.driverjs-theme .driver-popover-navigation-btns {
  justify-content: space-between;
  gap: 3px;
  color: var(--midNight-blue);
}

.driver-popover.driverjs-theme .driver-popover-close-btn {
  color: #9b9b9b;
}
.driver-popover.driverjs-theme .driver-popover-close-btn:hover {
  transform: rotate(360);
  transition: all 1s ease;
  /* background: red; */
}

.driver-popover.driverjs-theme .driver-popover-close-btn:hover {
  color: rgb(255, 255, 255);
}

.driver-popover.driverjs-theme
  .driver-popover-arrow-side-left.driver-popover-arrow {
  border-left-color: #c298fa;
}

.driver-popover.driverjs-theme
  .driver-popover-arrow-side-right.driver-popover-arrow {
  border-right-color: #c298fa;
}

.driver-popover.driverjs-theme
  .driver-popover-arrow-side-top.driver-popover-arrow {
  border-top-color: #c298fa;
}

.driver-popover.driverjs-theme
  .driver-popover-arrow-side-bottom.driver-popover-arrow {
  border-bottom-color: #c298fa;
}

.driver-fade .driver-popover {
  animation: rotate 2s ease !important ;
}

